import { createFeatureSelector, createSelector } from '@ngrx/store';
import { IViewDesignerState } from '../interfaces/view-designer-state.interface';
import { IViewDesignModel } from '../interfaces/view-design-model.interface';
import { get } from 'lodash';

export const viewDesignerState = createFeatureSelector<IViewDesignerState>('viewDesigner');

export const viewDesignerModelsSelector = createSelector(viewDesignerState, (viewDesigner) => viewDesigner.model);

export const viewModelSelector = createSelector(viewDesignerModelsSelector, (models) => models.viewDesignModel);

export const viewModelPropertyValueSelector = createSelector(
  viewModelSelector,
  (viewModel: IViewDesignModel, { propertyName }: { propertyName: string }) => get(viewModel, propertyName)
);

export const viewComponentModelsSelector = createSelector(
  viewDesignerModelsSelector,
  (models) => models.viewComponentDesignModels
);

export const firstViewComponentModelTypeSelector = createSelector(
  viewDesignerModelsSelector,
  (models) => Object.values(models.viewComponentDesignModels)[0]?.type
);

export const viewComponentModelSelector = createSelector(
  viewComponentModelsSelector,
  (componentModels, { guid }: { guid: string }) => componentModels[guid]
);

export const parentComponentModelGuidSelector = createSelector(
  viewComponentModelSelector,
  (componentModel) => componentModel?.parentGuid
);

export const componentPropertiesByNameSelector = createSelector(
  viewComponentModelsSelector,
  (componentModels, { guid, propertyName }: { guid: string; propertyName?: string }) =>
    componentModels[guid]
      ? propertyName
        ? get(componentModels[guid].propertiesByName, propertyName)
        : componentModels[guid].propertiesByName
      : null
);

export const componentTypeSelector = createSelector(viewComponentModelsSelector, (componentModels, { guid }) =>
  componentModels[guid] ? componentModels[guid].type : null
);

export const componentLayoutSelector = createSelector(
  viewComponentModelSelector,
  (componentModel) => componentModel?.layout
);

export const componentPermissionsSelector = createSelector(
  viewComponentModelSelector,
  (componentModel) => componentModel?.permissions
);

export const childDataComponentGuidsSelector = createSelector(
  viewComponentModelSelector,
  (componentModel) => componentModel?.childDataComponentGuids
);

export const viewDesignerUISelector = createSelector(viewDesignerState, (viewDesigner) => viewDesigner.ui);

export const isViewDefinitionLoadingSelector = createSelector(
  viewDesignerUISelector,
  (ui) => ui.isViewDefinitionLoading
);

export const bundleFriendlyNameSelector = createSelector(viewDesignerUISelector, (ui) => ui.friendlyBundleName);

export const currentBundleIdSelector = createSelector(viewDesignerUISelector, (ui) => ui.currentBundleId);

export const viewDefinitionSelector = createSelector(viewDesignerState, (viewDesigner) => viewDesigner.viewDefinition);

export const validationIssuesSelector = createSelector(viewDesignerState, (viewDesigner) => viewDesigner.validation);

export const breadcrumbsSelector = createSelector(viewDesignerState, (viewDesigner) => viewDesigner.breadcrumbs);

export const selectedInspectorTabIdSelector = createSelector(viewDesignerUISelector, (ui) => ui.selectedInspectorTabId);

export const selectedComponentGuidSelector = createSelector(viewDesignerUISelector, (ui) => ui.selectedComponentGuid);

export const areViewModelsReadySelector = createSelector(viewDesignerUISelector, (ui) => ui.areViewModelsReady);

export const selectedComponentPropsSelector = createSelector(
  selectedComponentGuidSelector,
  viewComponentModelsSelector,
  (selectedComponentGuid, componentModels) => {
    if (componentModels && selectedComponentGuid) {
      const componentDesignModel = componentModels[selectedComponentGuid];

      return componentDesignModel ? componentDesignModel.propertiesByName : null;
    } else {
      return null;
    }
  }
);
